@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+TC|Red+Hat+Text&display=swap);
a, a:hover, a:focus {
  color: inherit;
  text-decoration: none;
}

body {
  margin: 0;
  font-family: 'Red Hat Text', 'Noto Sans TC', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #1f1f1f;
  padding-top: 90px;
}

main {
  min-height: 90vh;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fff;
  box-shadow: 0 1px 10px rgba(0,0,0,0.1);
}

.h-1 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header .nav li {
  position: relative;
  padding: 0 20px;
}

.header .nav li a {
  display: flex;
  height: 90px;
  align-items: center;
  justify-content: center;
}

.footer {
  padding: 90px 0;
  background-color: #f3f3f3f3;
}

.totop {
  outline: none !important;
  background: rgba(255,255,255,0.5);
  width: 40px;
  height: 40px;
  position: fixed;
  right: 20px;
  bottom: 20px;
  font-size: 0;
  border: 0;
  border-radius: 0;
}

